import jss from "jss";
import preset from "jss-preset-default";

console.info("hello");

jss.setup(preset());

// Create your style.
const style = {
  myButton: {
    color: "green",
  },
  "@global": {
    "#bcpo": {
      marginLeft: "15px",
    },
    "select.single-option-selector": {
      borderColor: "black",
    },
  },
};

// Compile styles, apply plugins.
const sheet = jss.createStyleSheet(style);

// If you want to render on the client, insert it into DOM.
sheet.attach();

// If you want to render server-side, get the CSS text.
// sheet.toString();
// console.log(sheet.toString());

//1593792000000 is 2020-6-4
//this removes liquid error text on page
if (new Date().getTime() > 1593792000000)
  $x("//div/text()")
    .filter((e) => e.textContent.includes("Liquid error:"))
    .forEach((e) => (e.textContent = ""));

if (1 == 1) {
  //handle description tab content
  const description = $('div[itemprop="description"]').html();
  //some pages do not contain description
  if (description.trim()) $("#tab1>div").html(description).addClass("clearfix");
}
